@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: "Titillium Web", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  padding: 0;
}
body::-webkit-scrollbar {
  width: 0.7em;
}
a {
  text-decoration: none;
}
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(#c21717, #d77979);
  background-color: darkgrey;
  outline: none;
  border-radius: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footerman img{width:100%}.footerman .footer{background:#2b2d3b;display:flex;justify-content:space-around;color:gray}.footerman .footer p{padding-bottom:10px;padding-top:20px}.footerman .footer p span{color:white}

.MainHomeCarousel{margin-top:-100px;position:relative;z-index:-999}.carousel-item::after{position:absolute;content:"";top:0;width:100%;height:90%;left:0;background:linear-gradient(to top, transparent, rgba(0,0,0,0.678));background:-webkit-linear-gradient(to top, transparent, 0.678)}.carousel-fade .carousel-item{opacity:0;transition-duration:0.6s;transition-property:opacity}.carousel-fade .carousel-item.active,.carousel-fade .carousel-item-next.carousel-item-left,.carousel-fade .carousel-item-prev.carousel-item-right{opacity:1}.carousel-fade .active.carousel-item-left,.carousel-fade .active.carousel-item-right{opacity:0}.carousel-fade .carousel-item-next,.carousel-fade .carousel-item-prev,.carousel-fade .carousel-item.active,.carousel-fade .active.carousel-item-left,.carousel-fade .active.carousel-item-prev{transform:translateX(0);transform:translate3d(0, 0, 0)}.h-100vh{height:100vh}

.BottomNavbar{position:absolute;color:white;z-index:1;font-family:"Titillium Web", sans-serif !important;font-weight:600}.BottomNavbar .BottomNavbar__items{padding:20px 120px 10px 120px}.BottomNavbar ul{list-style:none;display:flex}.BottomNavbar ul .BottomNavbar__homeIcon{font-size:20px}.BottomNavbar ul li{margin-left:1rem;margin-right:1.2rem}.BottomNavbar ul li p{font-size:19px}.BottomNavbar ul li p i{font-size:15px}.BottomNavbar ul li i{margin-left:3px}

nav{background:linear-gradient(to top, black);font-family:"Titillium Web", sans-serif !important;padding:10px 150px !important}nav .navbar__brand{display:flex;align-items:center}nav .navbar__brand::after{position:absolute;content:"";z-index:-1;top:0;width:100%;height:70%;left:0;background:linear-gradient(to top, transparent, black);background:-webkit-linear-gradient(to top, transparent, black)}nav .navbar__brand .navbar__logo{width:80px}nav .navbar__brand h1{color:white;font-weight:bold;font-size:28px;padding-left:5px}nav .navbar__address{display:flex;color:white;line-height:5px;align-items:center}nav .navbar__address p{font-weight:100;font-size:13px}nav .navbar__address span{font-size:13px;font-weight:100}nav .navbar__address i{font-size:50px;margin-left:10px}

.TopNavbar{background:#c21717;color:white;font-size:14px;display:flex;justify-content:space-around;align-items:center !important;padding-left:50px;padding-right:50px;font-family:"Titillium Web", sans-serif !important;font-weight:bold}.TopNavbar ul{padding:5px;margin:0;list-style:none;display:flex !important}.TopNavbar ul li{align-items:center;display:flex;margin-left:10px}.TopNavbar ul li a{color:white;text-decoration:none !important}.TopNavbar .TopNavbar__icon{font-size:18px;margin-top:-2px;margin-left:5px;margin-right:5px}.TopNavbar .TopNavbar__social-media li{margin-left:10px;margin-right:10px}

.MainHeadText{position:absolute;color:white;top:40%;left:18%}.MainHeadText h1{font-family:"Monoton";font-size:80px}

.servicelink1{display:flex;padding-bottom:50px;justify-content:center;margin-top:-180px}.servicelink1 h4{font-size:20px}.servicelink1 .servicelink1__card{padding:30px 15px;text-align:center;width:330px;height:230px}.servicelink1 .servicelink1__card i{font-size:70px}

.importantannouncement{margin-left:110px;margin-right:110px;margin-bottom:90px}.important-announcement{margin-top:20px}.important-announcement h1{text-transform:uppercase;text-align:center;font-size:1.8rem;font-weight:700}.important-announcement h1 span{color:#c21717}.hrline{border-radius:8px;bottom:0;content:"";background-color:#c21717;height:2px;left:0;right:0px;margin:20px auto;position:relative;width:64px}.important-announcement__img img{width:100%;height:200px}.importantannouncement-link{margin-top:50px}.importantannouncement-link h2{color:#363636;font-size:20px}.importantannouncement-link h2 .icon{margin-top:-5px}.importantannouncement-link h3{font-size:18px;color:#414141}.iacard{margin-top:70px;display:flex}.iacard .iacard__delhi{display:flex;width:750px}.iacard .iacard__delhi h3{font-weight:bold}.iacard .iacard__delhi h3 span{color:#c21717}.iacard .iacard__delhi p{font-size:17px}.iacard .iacard__delhi button{border:0.5px solid #a5a5a5;border-radius:5px;padding:5px;padding-left:15px;padding-right:15px;color:#535353;font-size:15px;outline:none;background:white}.ia__dc{width:550px;padding:20px;background-color:#fff4f4;margin-left:20px}.ia__dc h3{font-weight:bold}.ia__dc h3 span{color:#c21717}.ia__dc .ia__dc_child{display:flex}.ia__dc .ia__dc_child img{margin-right:10px}.ia__dc button{border:0.5px solid #a5a5a5;border-radius:5px;padding:5px;padding-left:15px;padding-right:15px;color:#535353;font-size:15px;outline:none;background:white}

.icon-bar{position:fixed;top:50%;right:0;transform:translateY(-50%)}.icon-bar p{-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;padding-bottom:8px}.icon-bar i{transform:rotateZ(90deg);margin-left:15px}.icon-bar a{display:block;text-align:center;padding-bottom:0px;transition:all 0.3s ease;color:white;text-decoration:none;width:50px;font-size:20px}.icon-bar a:hover{background-color:#000}.facebook{background:#c21717;color:white}

